body {
  color: #615551;
  background-color: #fff;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

.container {
  background-color: #fff;
  grid-template: "head head"
                 "list main" 1fr
                 / 1fr 2fr;
  width: 100%;
  height: 100vh;
  margin: 0;
  display: grid;
}

.header {
  grid-area: head;
  width: 100%;
}

.search-results {
