  overflow-y: auto;
}

.btn--small, .btn--small:link, .btn--small:visited, .btn {
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-image: linear-gradient(to bottom right, #fbdb89, #f48982);
  border: none;
  border-radius: 10rem;
  align-items: center;
  transition: all .2s;
  display: flex;
}

.btn--small:hover, .btn:hover {
