  margin-right: 1rem;
}

.btn {
  padding: 1.5rem 4rem;
  font-size: 1.5rem;
  font-weight: 600;
}

.btn svg {
  fill: currentColor;
  width: 2.25rem;
  height: 2.25rem;
}

.btn--small, .btn--small:link, .btn--small:visited {
  padding: 1.25rem 2.25rem;
  font-size: 1.4rem;
